.square {
    background: #fff;
    border: 1px solid #999;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    width: 34px;
    padding: 0;
    text-align: center;
    cursor: pointer;
  }
  
  .winning-square {
    background: lightgreen;
  }
  